@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
	--primary-white: #fff;
	--primary-orange: #f98b00;
	--primary-dark: rgba(255, 255, 255, 0);
}

body {
	font-family: 'Roboto Condensed' sans-serif;
}

/* Navbar */

.logo {
	width: 16.25rem;
	font-size: 2em;
	color: #fff !important;
	color: var(--primary-white) !important;
}

/* Navbar link */

nav a.nav-link {
	font-weight: 500;
	letter-spacing: 0.0375rem;
	cursor: pointer;
}

nav ul li {
	text-transform: uppercase;
}

.navbar-nav > li > a {
	margin-right: 0.75rem;
	margin-left: 0.75rem;
	color: #fff !important;
	color: var(--primary-white) !important;
	border-top: 0.1875rem solid rgba(255, 255, 255, 0);
	border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
	border-top: 0.1875rem solid #f98b00;
	border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
	border-top: 0.1875rem solid #f98b00;
	border-top: 0.1875rem solid var(--primary-orange);
}

.hamburger {
	background-color: #fff;
}

